import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const roadsterPath = "/roadster";
export const getData = props => () => {
  const promise = window.client.getRoadster(props.id);
  return {
    roadster: wrapPromise(promise)
  };
};
const Component = () => import("./Roadster");
const RoadsterPage = ({ ...props }) => (
  <Page getComponent={Component} getData={getData(props)} />
);

export default RoadsterPage;
