import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const aboutPath = "/about";
export const getData = () => {
  const promise = window.client.getInfo();
  return {
    info: wrapPromise(promise)
  };
};
const Component = () => import("./About");
const AboutPage = () => <Page getComponent={Component} getData={getData} />;

export default AboutPage;
