import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const getData = props => () => {
  const promise = window.client.getHistoryEvent(props.id);
  return {
    event: wrapPromise(promise)
  };
};
const Component = () => import("./HistoryEvent");
const HistoryEventPage = ({ ...props }) => (
  <Page getComponent={Component} getData={getData(props)} />
);

export default HistoryEventPage;
