import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const sendToMarsPath = "sendToMars";
export const getData = props => () => {
  const promise = window.client.getSentToMars();
  return {
    items: wrapPromise(promise)
  };
};
const Component = () => import("./SendToMars");
const SendToMarsPage = ({ ...props }) => (
  <Page getComponent={Component} getData={getData(props)} />
);

export default SendToMarsPage;
