import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const getData = props => () => {
  const promise = window.client.getRocket(props.id);
  return {
    rocket: wrapPromise(promise)
  };
};
const Component = () => import("./Rocket");
const RocketPage = ({ ...props }) => (
  <Page getComponent={Component} getData={getData(props)} />
);

export default RocketPage;
