import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const historyPath = "/history";
export const getData = () => {
  const promise = window.client.getHistory();
  return {
    history: wrapPromise(promise)
  };
};
const Component = () => import("./History");
const HistoryPage = () => <Page getComponent={Component} getData={getData} />;

export default HistoryPage;
