import React from "react";
import Page from "../../components/Page";
import { wrapPromise } from "../../constants";

export const rocketsPath = "/rockets";
export const getData = () => {
  const promise = window.client.getRockets();
  return {
    rockets: wrapPromise(promise)
  };
};
const Component = () => import("./Rockets");
const RocketsPage = () => <Page getComponent={Component} getData={getData} />;

export default RocketsPage;
