import React, { useState, useEffect } from "react";
import { Router } from "@reach/router";
import Layout from "./components/Layout";
import { UserContext } from "./constants";
import LoginPage, { loginPath } from "./pages/Login";
import AboutPage, { aboutPath } from "./pages/About";
import HistoryPage, { historyPath } from "./pages/History";
import HistoryEventPage from "./pages/HistoryEvent";
import RocketsPage, { rocketsPath } from "./pages/Rockets";
import RocketPage from "./pages/Rocket";
import RoadsterPage, { roadsterPath } from "./pages/Roadster";
import SendToMarsPage, { sendToMarsPath } from "./pages/SendToMars";

const App = () => {
  const [username, setUsername] = useState(null);
  useEffect(() => {
    window.client.getUser().then(username => {
      setUsername(username);
    }).catch(() => {});
  }, []);
  return (
    <UserContext.Provider value={[username, setUsername]}>
      <Layout>
        <ErrorBound>
          <Router>
            <NotFound default />
            <LoginPage path={loginPath} />
            {username ? <AboutPage path={aboutPath} /> : null}
            {username ? <HistoryPage path={historyPath} /> : null}
            {username ? <HistoryEventPage path={`${historyPath}/:id`} /> : null}
            {username ? <RocketsPage path={rocketsPath} /> : null}
            {username ? <RocketPage path={`${rocketsPath}/:id`} /> : null}
            {username ? <RoadsterPage path={roadsterPath} /> : null}
            {username ? <SendToMarsPage path={sendToMarsPath} /> : null}
          </Router>
        </ErrorBound>
      </Layout>
    </UserContext.Provider>
  );
};

class ErrorBound extends React.Component {
  state = { error: null };

  render() {
    const { error } = this.state;
    const { children } = this.props;
    if (error) console.error(error);
    return error ? (
      <div>
        <h1>Error</h1>
        <pre>{error.stack}</pre>
      </div>
    ) : (
      <>{children}</>
    );
  }

  componentDidCatch(error) {
    this.setState({ error });
  }
}

const NotFound = () => <h1>Sorry, nothing here</h1>;

export default App;
