import React from "react";

export const SUSPENSE_CONFIG = { timeoutMs: 1000 };

export const UserContext = React.createContext([null, () => void 0]);

export const wrapPromise = promise => {
  let status = "pending";
  let result;
  let suspender = promise.then(
    r => {
      status = "success";
      result = r;
    },
    e => {
      status = "error";
      result = e;
    }
  );
  return {
    read() {
      if (status === "pending") {
        throw suspender;
      } else if (status === "error") {
        throw result;
      } else if (status === "success") {
        return result;
      }
    }
  };
};
