import { Link as NavLink, navigate } from "@reach/router";
import { SUSPENSE_CONFIG } from "../../constants";
import React, { useTransition } from "react";
import cs from "./Link.module.css";

const Link = ({ to, ...rest }) => {
  const [startTransition] = useTransition(SUSPENSE_CONFIG);
  return (
    <NavLink
      onClick={e => {
        e.preventDefault();
        startTransition(() => {
          navigate(to);
        });
      }}
      to={to}
      className={cs.link}
      getProps={({ isCurrent }) =>
        isCurrent
          ? {
              style: {
                color: "var(--accent)"
              }
            }
          : null
      }
      {...rest}
    />
  );
};

export default Link;
